export interface MemberReport {
  /**
   * Report ID.
   * @format GUID
   * @readonly
   */
  id?: string | null;
  /**
   * ID of the reported member.
   * @format GUID
   */
  reportedMemberId?: string;
  /**
   * ID of the member who created the report.
   * @readonly
   */
  reportingMemberId?: string | null;
  /** Reason for reporting a member. */
  reason?: Reason;
  /**
   * Date and time the report was created.
   * @readonly
   */
  createdDate?: Date | null;
}

export interface Reason {
  /** Report reason type. */
  category?: CategoryWithLiterals;
  /** Explanation of why the member is being reported. */
  description?: string | null;
}

export enum Category {
  /** Unknown category. This value is unused. */
  UNKNOWN = 'UNKNOWN',
  /** The member is reported for spam. */
  SPAM = 'SPAM',
  /** The member is reported for impersonation. */
  IMPERSONATION = 'IMPERSONATION',
  /** The member is reported for harassment. */
  HARASSMENT = 'HARASSMENT',
  /** The member is reported for other reasons. */
  OTHER = 'OTHER',
}

/** @enumType */
export type CategoryWithLiterals =
  | Category
  | 'UNKNOWN'
  | 'SPAM'
  | 'IMPERSONATION'
  | 'HARASSMENT'
  | 'OTHER';

export interface ReportMemberRequest {
  /** Details of a member to report. */
  memberReport: MemberReport;
}

export interface ReportMemberResponse {
  /** Member report. */
  memberReport?: MemberReport;
}

export interface ReportedMemberCreated {
  reportedMember?: ReportedMember;
}

/** Reported member details. */
export interface ReportedMember {
  /**
   * ID of the reported member.
   * @format GUID
   */
  reportedMemberId?: string;
  /** Number of reports for this member. */
  reportCount?: string;
  /** Whether reports of this member have been reviewed. */
  reviewed?: boolean;
  /**
   * Date and time when the most recent report was created.
   * @readonly
   */
  lastReportDate?: Date | null;
}

export interface SelfReportingForbiddenError {
  /**
   * ID of the member that is reported.
   * @format GUID
   */
  memberId?: string;
}

export interface CannotReportBlockedMemberError {
  /**
   * ID of the member that is reported.
   * @format GUID
   */
  memberId?: string;
}

export interface QueryMemberReportsRequest {
  /** Query options. See [API Query Language](https://dev.wix.com/docs/rest/articles/getting-started/api-query-language) for more details. */
  query?: QueryV2;
}

export interface QueryV2 {
  /**
   * Filter object in the following format:
   * `"filter" : {
   * "fieldName1": "value1",
   * "fieldName2":{"$operator":"value2"}
   * }`
   * Example of operators: `$eq`, `$ne`, `$lt`, `$lte`, `$gt`, `$gte`, `$in`, `$hasSome`, `$hasAll`, `$startsWith`, `$contains`
   *
   * See [API Query Language](https://dev.wix.com/docs/rest/articles/getting-started/api-query-language) for more details.
   */
  filter?: Record<string, any> | null;
  /**
   * Sort object in the following format:
   * `[{"fieldName":"sortField1","order":"ASC"},{"fieldName":"sortField2","order":"DESC"}]`
   */
  sorting?: Sorting[];
  /** Paging options to limit and skip the number of items. */
  paging?: CursorPaging;
}

export interface Sorting {
  /**
   * Name of the field to sort by.
   * @maxLength 512
   */
  fieldName?: string;
  /** Sort order. */
  order?: SortOrderWithLiterals;
}

export enum SortOrder {
  ASC = 'ASC',
  DESC = 'DESC',
}

/** @enumType */
export type SortOrderWithLiterals = SortOrder | 'ASC' | 'DESC';

export interface CursorPaging {
  /**
   * The number of items to load.
   * Default: `100`
   * @max 1000
   */
  limit?: number | null;
  /** Cursor token pointing to a page of results. Not used in the first request. Following requests use the cursor token and not `filter` or `sort`. */
  cursor?: string | null;
}

export interface QueryMemberReportsResponse {
  /** Retrieved member reports. */
  memberReports?: MemberReport[];
  /** Metadata for the paginated results. */
  pagingMetadata?: PagingMetadataV2;
}

export interface PagingMetadataV2 {
  /** Number of items returned in the response. */
  count?: number | null;
  /** Offset that was requested. */
  offset?: number | null;
  /** Total number of items that match the query. Returned if offset paging is used and the `tooManyToCount` flag is not set. */
  total?: number | null;
  /** Flag that indicates the server failed to calculate the `total` field. */
  tooManyToCount?: boolean | null;
  /** Cursors to navigate through the result pages using `next` and `prev`. Returned if cursor paging is used. */
  cursors?: Cursors;
}

export interface Cursors {
  /**
   * Cursor string pointing to the next page in the list of results.
   * @maxLength 16000
   */
  next?: string | null;
  /**
   * Cursor pointing to the previous page in the list of results.
   * @maxLength 16000
   */
  prev?: string | null;
}

export interface MarkMemberReportsAsReviewedRequest {
  /**
   * ID of the member whose reports are to be marked as reviewed.
   * @format GUID
   */
  memberId?: string;
}

export interface MarkMemberReportsAsReviewedResponse {
  /** Reported member details. */
  reportedMember?: ReportedMember;
}

export interface ListReportedMembersRequest
  extends ListReportedMembersRequestPagingMethodOneOf {
  /** Cursor token pointing to a page of results. Not used in the first request. Following requests use the cursor token and not `filter` or `sort`. */
  cursorPaging?: CursorPaging;
}

/** @oneof */
export interface ListReportedMembersRequestPagingMethodOneOf {
  /** Cursor token pointing to a page of results. Not used in the first request. Following requests use the cursor token and not `filter` or `sort`. */
  cursorPaging?: CursorPaging;
}

export interface Paging {
  /**
   * Number of items to load.
   * @max 100
   */
  limit?: number | null;
  /** Number of items to skip in the current sort order. */
  offset?: number | null;
}

export interface ListReportedMembersResponse {
  /** Retrieved reported members. */
  reportedMembers?: ReportedMember[];
  /** Metadata for the paginated results. */
  pagingMetadata?: PagingMetadataV2;
}

export interface QueryReportedMembersRequest {
  /** Query options. See [API Query Language](https://dev.wix.com/docs/rest/articles/getting-started/api-query-language) for more details. */
  query?: QueryV2;
}

export interface QueryReportedMembersResponse {
  /** Retrieved reported members. */
  reportedMembers?: ReportedMember[];
  /** Metadata for the paginated results. */
  pagingMetadata?: PagingMetadataV2;
}

export interface DeleteMemberReportsRequest {
  /**
   * ID of the member whose reports are to be deleted.
   * @format GUID
   */
  memberId: string;
}

export interface DeleteMemberReportsResponse {}

export interface ReportedMemberDeleted {
  /** @format GUID */
  reportedMemberId?: string;
}

export interface DomainEvent extends DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
  /** Event ID. With this ID you can easily spot duplicated events and ignore them. */
  id?: string;
  /**
   * Fully Qualified Domain Name of an entity. This is a unique identifier assigned to the API main business entities.
   * For example, `wix.stores.catalog.product`, `wix.bookings.session`, `wix.payments.transaction`.
   */
  entityFqdn?: string;
  /**
   * Event action name, placed at the top level to make it easier for users to dispatch messages.
   * For example: `created`/`updated`/`deleted`/`started`/`completed`/`email_opened`.
   */
  slug?: string;
  /** ID of the entity associated with the event. */
  entityId?: string;
  /** Event timestamp in [ISO-8601](https://en.wikipedia.org/wiki/ISO_8601) format and UTC time. For example, `2020-04-26T13:57:50.699Z`. */
  eventTime?: Date | null;
  /**
   * Whether the event was triggered as a result of a privacy regulation application
   * (for example, GDPR).
   */
  triggeredByAnonymizeRequest?: boolean | null;
  /** If present, indicates the action that triggered the event. */
  originatedFrom?: string | null;
  /**
   * A sequence number that indicates the order of updates to an entity. For example, if an entity was updated at 16:00 and then again at 16:01, the second update will always have a higher sequence number.
   * You can use this number to make sure you're handling updates in the right order. Just save the latest sequence number on your end and compare it to the one in each new message. If the new message has an older (lower) number, you can safely ignore it.
   */
  entityEventSequence?: string | null;
}

/** @oneof */
export interface DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
}

export interface EntityCreatedEvent {
  entityAsJson?: string;
  /** Indicates the event was triggered by a restore-from-trashbin operation for a previously deleted entity */
  restoreInfo?: RestoreInfo;
}

export interface RestoreInfo {
  deletedDate?: Date | null;
}

export interface EntityUpdatedEvent {
  /**
   * Since platformized APIs only expose PATCH and not PUT we can't assume that the fields sent from the client are the actual diff.
   * This means that to generate a list of changed fields (as opposed to sent fields) one needs to traverse both objects.
   * We don't want to impose this on all developers and so we leave this traversal to the notification recipients which need it.
   */
  currentEntityAsJson?: string;
}

export interface EntityDeletedEvent {
  /** Entity that was deleted. */
  deletedEntityAsJson?: string | null;
}

export interface ActionEvent {
  bodyAsJson?: string;
}

export interface Empty {}

export interface MessageEnvelope {
  /**
   * App instance ID.
   * @format GUID
   */
  instanceId?: string | null;
  /**
   * Event type.
   * @maxLength 150
   */
  eventType?: string;
  /** The identification type and identity data. */
  identity?: IdentificationData;
  /** Stringify payload. */
  data?: string;
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /** @readonly */
  identityType?: WebhookIdentityTypeWithLiterals;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}

export enum WebhookIdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

/** @enumType */
export type WebhookIdentityTypeWithLiterals =
  | WebhookIdentityType
  | 'UNKNOWN'
  | 'ANONYMOUS_VISITOR'
  | 'MEMBER'
  | 'WIX_USER'
  | 'APP';
/** @docsIgnore */
export type ReportMemberApplicationErrors =
  | {
      code?: 'SELF_REPORTING_FORBIDDEN';
      description?: string;
      data?: SelfReportingForbiddenError;
    }
  | {
      code?: 'CANNOT_REPORT_BLOCKED_MEMBER';
      description?: string;
      data?: CannotReportBlockedMemberError;
    };
/** @docsIgnore */
export type DeleteMemberReportsApplicationErrors = {
  code?: 'CANNOT_DELETE_MEMBER_REPORTS';
  description?: string;
  data?: Record<string, any>;
};
